@import '~assets/styles/h3Imports';

@mixin activePrescription {
  .active-prescriptions {
    background-color: white;
    max-width: rem(343);
    padding-bottom: rem(8);
    padding-top: rem(24);
    width: 100%;

    .segmented-question {
      .question {
        font-size: rem(14);
        padding-bottom: rem(8);
      }

      .segmented {
        & * {
          border-width: 2px;
        }

        padding-bottom: rem(24);

        :first-child {
          height: rem(40);
          max-width: 100%;

          label {
            height: 100%;
          }

          :first-child,
          :last-child {
            align-items: center;
            display: inline-flex;
            font-size: rem(18);
            justify-content: center;
            padding: 0;
          }
        }
      }
    }

    .form {
      p {
        display: none;
      }

      * {
        max-width: unset;
      }

      button {
        display: none;
      }
    }

    @media #{$tablet-up} {
      display: flex;
      justify-content: space-between;
      max-width: rem(720);

      .segmented-question,
      .form {
        width: calc(50% - 20px);
      }

      .segmented-question {
        margin-right: rem(40);
        max-width: rem(306);

        .segmented {
          & * {
            border-width: 2px;
          }

          padding-bottom: rem(24);

          :first-child {
            height: rem(48);
            max-width: rem(316);

            /* stylelint-disable-next-line max-nesting-depth */
            :first-child {
              height: rem(48);
              max-width: rem(158);
            }
          }
        }
      }
    }
  }

  .not-visible {
    display: none;
  }
}
