@import '~assets/styles/h3Imports';

.root {
  background-color: var(--white, #ffffff);

  .dropdown {
    --input-outline-color: var(--accent-2);

    margin-bottom: rem(16);
    max-width: rem(312);

    input {
      width: 100%;
    }

    & > :nth-child(2) > :nth-child(1) {
      height: auto;

      > :nth-child(3) {
        height: 100%;
        padding: 11px 12px;
      }
    }

    @media #{$medium-down} {
      max-width: unset;
      width: 100%;
    }

    [data-fontsize='AAA'] & {
      [data-input-control-element='necessity-indicator'] {
        margin-left: 0.125em;
      }
    }
  }

  p > span {
    color: var(--brand-primary, #e62600);
  }

  .button {
    align-content: center;
    display: flex;
    font-weight: bold;
    height: rem(40);
    justify-content: center;
    width: rem(120);

    @media #{$small-down} {
      display: inline-block;
      width: 100%;
    }
  }
}
